@import "../inc/config";
footer {
  display: flex;
  height: 50px;
  padding: 0 10px;
  background: $color1;
  position: fixed;
  border-radius: 15px 15px 0 0;
  width: 100%;
  bottom: 0;
  li {
    margin: 0 5px;
    width: 25%;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
    }
    &.active {
      a {
        position: relative;
        &:after {
          content: '';
          background: $color2;
          position: absolute;
          border-radius: 10px;
          width: 100%;
          height: 2px;
          bottom: 2px;
          left:0;
          right:0;
        }
        i {
          color:$color2;
        }
      }
    }
  }
}
